<script>
export default {
  name: 'clock',
  data: function () {
    return {
      sec: 15,
      timer: '308%'
    }
  },
  props: {
    msg: String
  },
  methods:{
    aaa(){
      return '308%'
    },
    setcircleProcess() {
      var process = this.$refs.process
      process.setAttribute('stroke-dashoffset',  308 - ( (this.sec) / 60) * 308 + '%');
      if (this.sec >= 60) {
        setTimeout(function(){
          this.sec = 0
          process.setAttribute('stroke-dashoffset', '308%');
          process.setAttribute('opacity', '0')
        }, 300); 
      }else{
        process.setAttribute('opacity', '1')
      }
    }
  },
  mounted () {

  }
}
</script>

